.dashboard_container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%; }

.dashboard_side_panel {
  display: flex;
  flex: 0 0 300px;
  flex-direction: column;
  widows: auto;
  height: 100%;
  background-color: #3f4a59; }

.dashboard_components_container {
  display: flex;
  padding: 20px; }

.dashboard_components {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column; }

.dashboard_components_header {
  display: flex;
  flex-direction: row;
  width: auto;
  height: 30px;
  padding: 10px 20px 10px 20px;
  color: rgba(3, 3, 3, 0.6);
  font-weight: 500;
  font-size: 0.8825rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 30px;
  text-transform: uppercase;
  background-color: #fff !important; }

.dashboard_side_panel_catregory {
  display: flex;
  flex-direction: row;
  height: 30px;
  margin-bottom: 20px;
  padding: 10px 20px 10px 20px;
  color: #fff;
  font-weight: 500;
  font-size: 0.8825rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 30px;
  text-transform: uppercase;
  background-color: #7348cc !important; }

.dashboard_side_panel_button {
  display: flex;
  width: 100%;
  height: 50px;
  padding: 10px 20px 10px 30px !important;
  color: white !important;
  background-color: transparent !important;
  border-radius: 0px !important; }
  .dashboard_side_panel_button:hover {
    color: white !important;
    background-color: #394250 !important; }
  .dashboard_side_panel_button span:first-child {
    display: flex;
    flex: 1 0 auto; }

.dashboard_side_panel_selected_button {
  display: flex;
  width: 100%;
  height: 50px;
  padding: 10px 20px 10px 30px !important;
  color: white !important;
  background-color: rgba(146, 109, 222, 0.35) !important;
  border-radius: 0px !important; }
  .dashboard_side_panel_selected_button:hover {
    color: white !important;
    background-color: rgba(146, 109, 222, 0.35) !important; }
  .dashboard_side_panel_selected_button span:first-child {
    display: flex;
    flex: 1 0 auto; }

.dashboard_component p {
  color: rgba(0, 0, 0, 0.68);
  font-weight: bold; }

.dashboard_component svg {
  margin-top: 20px;
  color: rgba(0, 0, 0, 0.26) !important;
  font-size: 130px !important; }
  .dashboard_component svg.dashboard_icon_checkcircle {
    color: #23ad75 !important; }
  .dashboard_component svg.dashboard_icon_removecircle {
    color: rgba(185, 21, 21, 0.8) !important; }

.dashboard_component img {
  width: 130px;
  height: 130px;
  margin-top: 20px; }

.dashboard_component button {
  margin-top: 20px; }
