.modal_content {
  margin-top: 30px;
  margin-bottom: 50px;
  font-weight: 400;
  font-family: 'Roboto';
  text-align: justify; }

.modal_overlay {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 100000;
  background-color: rgba(177, 177, 177, 0.55); }

.modal_header {
  flex: 1 0 auto;
  margin-bottom: 20px !important; }

.modal_close {
  position: absolute;
  top: 0.9rem;
  right: 0.5rem;
  display: flex;
  flex: 0 0 auto;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  text-decoration: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.25s linear; }
  .modal_close:before, .modal_close:after {
    position: absolute;
    content: '';
    width: 1.25rem;
    height: 0.125rem;
    background-color: black; }
  .modal_close:before {
    transform: rotate(-45deg); }
  .modal_close:after {
    transform: rotate(45deg); }
  .modal_close:hover, .modal_close:before, .modal_close:after, .modal_close:hover:before, .modal_close:hover:after, .modal_close:focus {
    border: none;
    outline: none; }
  .modal_close:hover, .modal_close:focus {
    color: black; }
  .modal_close:hover {
    transform: rotate(360deg); }
    .modal_close:hover:before, .modal_close:hover:after {
      background-color: tomato; }

.action_container {
  text-align: right; }

.action {
  position: relative;
  z-index: 1;
  margin-right: 10px;
  margin-left: 0.625rem;
  padding: 0.625rem 1.25rem;
  overflow: hidden;
  color: white;
  font-weight: 500;
  font-size: 0.87rem;
  font-family: "Roboto";
  background-color: slategray;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer; }
  .action:before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    content: '';
    width: 0%;
    height: 100%;
    background-color: rgba(32, 142, 230, 0.15); }
  .action:not(:last-child) {
    margin-right: 0;
    margin-left: 0;
    color: #208ee6;
    background-color: white; }
  .action:last-child {
    margin-right: 0;
    margin-left: 0;
    color: #d60808;
    background-color: white; }
  .action:not(:last-child):hover:before {
    width: 100%;
    background-color: rgba(32, 142, 230, 0.15); }
  .action:last-child:hover:before {
    width: 100%;
    background-color: rgba(214, 8, 8, 0.15); }
  .action:focus {
    outline: none; }

.flex_container {
  display: flex; }
